<template>
    <div>
      <!-- <header-breadcrumb :breadData="$route.matched" /> -->
      <nav-button>
        <el-button-group>
          <nav-premisson-button :comp="comp" :buttonData="buttonData"></nav-premisson-button>
        </el-button-group>
        <!-- <el-button-group>
          <el-button icon="fdddfont icon-shujudaoru" @click="exportsFile">导入</el-button>
        </el-button-group> -->
      </nav-button>
      <!-- 查询 -->
      <common-wrapper>
        <operatpage-search ref="search" class="search" @search="queryList" @rest="rest"/>
      </common-wrapper>
      <!-- 运营列表 -->
      <common-wrapper>
        <common-table :flex="245">
          <template slot="table">
            <el-table
              ref="roleTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="tableData"
              >
                <el-table-column type="index" :index="indexMethod" label="序号" min-width="50"></el-table-column>
                <el-table-column  label="司机姓名" min-width="100">
                  <template slot-scope="scope">{{scope.row.driverName || '' || null}}</template>
                </el-table-column>
                <el-table-column prop="busPlateNumber" label="车牌号" min-width="120"></el-table-column>
                <el-table-column prop="busSelfCode" label="自编号" min-width="100"></el-table-column>
                <el-table-column prop="busLine" label="线路" min-width="140"></el-table-column>
                <el-table-column prop="startTime" label="开始时间" min-width="180">
                  <template slot-scope="scope">{{scope.row.startTime | capitalize }}</template>
                </el-table-column>
                <el-table-column prop="endTime" label="结束时间"  min-width="180">
                   <template slot-scope="scope">{{scope.row.endTime | capitalize }}</template>
                </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
       <!-- 导入文件 -->
      <upload-file
         :showUpload.sync="showUpload"
         :uploadFileUrl="uploadFileUrl"
         :limit="limit"
         :size="size"
         :acceptType="acceptType"
         :pojo="pojo"
         :isShowDown="isShowDown"
         :isHasToken="isHasToken"
        @complete="getUploadData"/>
      <!-- 导入文件表格 -->
      <upload-file-table  :showUploadTable.sync="showUploadTable" :pojo="pojo" :uploadData.sync="uploadData" @updateList="querySearch" v-if="uploadData.data"/>
    </div>
</template>
<script>
/**
*  operatpage Module
* ----------------------
* Author: dengshijun
* Date: 2018.05.29
*/
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'

import operatpageSearch from './Components/Search'
// 导入文件模块
import UploadFile from '../../Common/Upload'
import UploadFileTable from '../../Common/UploadTable'
import UploadLog from '../../Common/UploadLog'
// api
import { operatSearch } from '@/api/management'
export default {
  name: 'OperatingInformation',
  mixins: [mixins],
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      isListLoading: false, // 列表加载
      // 导入需要传入的变量
      showUpload: false, // 导入窗口打开状态 ====>必填
      uploadFileUrl: '/excel/uploadFile', // 文件上传的url ====>必填
      limit: 1, // 上传文件的个数 默认为1
      size: 0.5, // 上传文件总的最大的容量（MB）默认为0.5
      acceptType: '.xls', // 上传文本格式  多个为'.xls,.png,.mp4' 默认为.xls
      pojo: 'TBusOperating', // 上传的额外参数（运营信息导入）===> (非必填) 可以是字符串，数字，对象 在上传模块接受到的额外参数将以对象形式上传 {pojo: this.pojo}
      isShowDown: true, // 是否显示下载模板 ===> (非必填)
      isHasToken: true, // 是否需要上传token ===> (非必填)
      // 导入完成后的表格模块变量
      showUploadTable: false, // 导入列表打开状态
      uploadData: {}, // 批量导入返回的数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  activated () {
    this.$refs.roleTable.doLayout()
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取列表
    getRoles (params) {
      this.isListLoading = true
      operatSearch(params).then(res => {
        this.tableData = res.list
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 批量导入
    exportsFile () {
      this.showUpload = true
    },
    // 批量导入数据
    getUploadData (params) {
      setTimeout(() => {
        this.showUploadTable = true
      }, 500)
      let promise = new Promise(function (resolve, reject) {
        resolve(params)
      })
      promise.then(res => {
        this.uploadData = res
      })
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    querySearch () {
      this.$refs.search.search()
    },
    // 导入查看log
    exportsLog () {
      this.$nextPage({
        title: `导入日志详情`,
        props: {
          modelName: '运营信息批量导入'
        },
        component: UploadLog, // 该组件会通过Webpack分开打包,并异步加载
        cache: true
      })
    }
  },
  filters: {
    capitalize: function (value) {
      if (!value) return ''
      return moment(value).utcOffset(8).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    operatpageSearch,
    UploadFile,
    UploadFileTable
  }
}
</script>



