<template>
     <el-form  :model="searchForm" ref="searchForm"  label-width="80px" >
         <el-row>
           <el-col :span="8">
            <el-form-item label="起始时间" prop="notifyTime">
                <el-date-picker
                  style="width:100%"
                  v-model="searchForm.notifyTime"
                  type="datetimerange"
                  :picker-options="dateBefore"
                  :default-time="[oftenTime, oftenTime]"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"/>
            </el-form-item>
           </el-col>
           <el-col :span="5">
              <el-form-item label="自编号" prop="busCode">
                <el-input v-model="searchForm.busCode" placeholder="请输入自编号"></el-input>
              </el-form-item>
           </el-col>
           <el-col :span="6">
             <el-form-item label="车牌号" prop="busNum">
                <el-select v-model="searchForm.busNum" placeholder="请选择">
                <el-option
                    v-for="item in buses"
                    :key="item.busUuid"
                    :label="item.busPlateNumber"
                    :value="item.busUuid">
                </el-option>
                </el-select>
          </el-form-item>
           </el-col>
           <el-col :span="5" :offset="0" class="flex-right">
                <el-form-item>
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-form-item>
            </el-col>
         </el-row>
      </el-form>
</template>

<script>
import moment from 'moment'
// api
import { busesList } from '@/api/common'
export default {
  name: 'operatpageSearch',
  data () {
    return {
      buses: [], // 车辆列表
      searchForm: {
        notifyTime: ['', ''],
        startTime: '',
        endTime: '',
        busCode: '',
        busNum: ''

      },
      dateBefore: {
        disabledDate: (time) => {
          return time.getTime() > new Date().getTime()
        }
      },
      oftenTime: moment().format('HH:mm:ss')
    }
  },
  watch: {
    'searchForm.notifyTime': function (time) {
      if (time.length) {
        this.searchForm.startTime = moment(time[0])
        this.searchForm.endTime = moment(time[1])
      }
    }
  },
  mounted () {
    busesList({}).then(res => {
      this.buses = res
    })
    this.initTime()
    this.search()
  },
  methods: {
    initTime () {
      const startTime = moment().subtract(6, 'd')
      const endTime = moment().subtract(0, 'd')
      this.searchForm.notifyTime = [startTime, endTime]
      this.searchForm.startTime = startTime
      this.searchForm.endTime = endTime
    },
    // 转换查询参数为后端需要的参数格式
    convertSearchParams () {
      const searchParams = {
        startTime: this.searchForm.startTime,
        endTime: this.searchForm.endTime,
        busCode: this.searchForm.busCode,
        busNum: this.searchForm.busNum
      }
      return searchParams
    },
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.convertSearchParams()))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      const params = {
        startTime: '',
        endTime: '',
        busCode: this.searchForm.busCode,
        busNum: this.searchForm.busNum
      }
      this.$emit('rest', params)
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



